:root {
  --cl-black: #0f1414;
  --cl-white: #ffffff;
  --cl-gray: #bfbfbf;
  --cl-red: #bc2e37;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: var(--cl-black);
  color: var(--cl-white);
  margin: 0;
  padding: 0;
}

main {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

section {
  min-height: 100vh;
}

.color-accent {
  color: var(--cl-red);
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../app/assets/fonts/OpenSans-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
body {
  font-family: "OpenSans", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: var(--cl-white);
  line-height: 1.8;
}

h1,
h2,
h3 {
  margin: 0;
}

a {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--cl-white);
  font-weight: 400;
  display: inline-block;
  padding-bottom: 6px;
  border-bottom: 1px solid var(--cl-red);
}

.h1 {
  font-size: 42px;
  text-align: center;
  margin-bottom: 4rem;
}

.h2 {
  font-size: 24px;
}

.h3 {
  font-size: 18px;
  color: var(--cl-white);
}

.text-light {
  font-weight: 300;
}

.text-bold {
  font-weight: 500;
}

.text-feather {
  font-weight: 300;
  color: var(--cl-gray);
}

.inset {
  font-size: 16px;
}

.uppercase {
  text-transform: uppercase;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  width: 100%;
}

.component_1 {
  max-height: 600px;
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "row1 row12 row2 " "row1 row22 row2";
  align-items: start;
}
.component_1 > * {
  height: 100%;
}

.row-1 {
  grid-area: row1;
}

.row-1-2 {
  grid-area: row12;
}

.row-2-2 {
  grid-area: row22;
}

.row-2 {
  grid-area: row2;
}

.img-underlined {
  border-bottom: 4px solid var(--cl-red);
  margin-bottom: 2rem;
}